     v-else\r\n                        @click=\"doEdit(true)\"\r\n                        class=\"btn btn-sm wed-btn-main\"\r\n                        title=\"add new task\"\r\n                    >\r\n                        + add Tasks\r\n                    </button> -->\r\n                </div>\r\n            </div>\r\n            <div\r\n                v-if=\"success\"\r\n                class=\"alert alert-success alert-dismissible fade show mt-3\"\r\n                role=\"alert\"\r\n            >\r\n                Task Saved !\r\n                <button\r\n                    type=\"button\"\r\n                    class=\"close\"\r\n                    data-dismiss=\"alert\"\r\n                    aria-label=\"Close\"\r\n                >\r\n                    <span aria-hidden=\"true\">&times;</span>\r\n                </button>\r\n            </div>\r\n            <div class=\"wed-host-section-container-form\" v-if=\"editing\">\r\n                <div class=\"alert alert-danger\" role=\"alert\" v-if=\"hasError\">\r\n                    All fields are required!\r\n                </div>\r\n\r\n                <form class=\"form-row align-items-center\">\r\n                    <div class=\"form-group-custom\">\r\n                        <label for=\"title\" class=\"form-label\">New Task</label>\r\n                        <input\r\n                            @keyup.enter=\"saveTask\"\r\n                            type=\"text\"\r\n                            name=\"title\"\r\n                            id=\"title\"\r\n                            class=\"form-control\"\r\n                            v-model=\"fields.title\"\r\n                            placeholder=\"Add an new Task\"\r\n                        />\r\n                        <div v-if=\"errors && errors.title\" class=\"text-danger\">\r\n                            {{ errors.title[0] }}\r\n                        </div>\r\n                    </div>\r\n                    <div class=\"form-group-custom\">\r\n                        <label class=\"form-label\">Task Category</label>\r\n                        <select\r\n                            name=\"category_id\"\r\n                            class=\"form-control\"\r\n                            v-model=\"fields.category_id\"\r\n                        >\r\n                            <option\r\n                                v-for=\"(category, id) in categories\"\r\n                                :key=\"id\"\r\n                                :value=\"category.id\"\r\n                            >\r\n                                {{ category.name }}\r\n                            </option>\r\n                        </select>\r\n                    </div>\r\n                    <div class=\"form-group-custom\">\r\n                        <label class=\"form-label\">Assigned To</label>\r\n                        <div class=\"custom-select-box\">\r\n                            <span @click.stop=\"showDropdown = !showDropdown\">{{\r\n                                selectedMember?.name\r\n                            }}</span>\r\n                            <div v-if=\"showDropdown\">\r\n                                <span\r\n                                    v-for=\"(mem, id) in members\"\r\n                                    :key=\"id\"\r\n                                    :value=\"mem.id\"\r\n                                    @click=\"\r\n                                        showDropdown = false;\r\n                                        selectedMember = mem;\r\n                                        fields.member_id = mem.id;\r\n                                    \"\r\n                                    >{{ mem.name }}\r\n                                </span>\r\n                            </div>\r\n                        </div>\r\n                        <!-- <select\r\n                            name=\"member_id\"\r\n                            class=\"form-control\"\r\n                            v-model=\"fields.member_id\"\r\n                        >\r\n                            <option\r\n                                v-for=\"(mem, id) in members\"\r\n                                :key=\"id\"\r\n                                :value=\"mem.id\"\r\n                            >\r\n                                {{ mem.name }}\r\n                            </option>\r\n                        </select> -->\r\n                    </div>\r\n                    <div\r\n                        class=\"form-group-custom\"\r\n                        style=\"margin-bottom: 10px; margin-top: 12px\"\r\n                    >\r\n                        <label class=\"form-label\">Task Priority</label>\r\n                        <br />\r\n                        <label class=\"form-label-radio\">\r\n                            <input\r\n                                type=\"radio\"\r\n                                class=\"form-radio\"\r\n                                name=\"taskPriority\"\r\n                                v-model=\"fields.taskPriority\"\r\n                                value=\"low\"\r\n                            />Low</label\r\n                        >\r\n                        <label class=\"form-label-radio\">\r\n                            <input\r\n                                type=\"radio\"\r\n                                class=\"form-radio\"\r\n                                v-model=\"fields.taskPriority\"\r\n                                value=\"medium\"\r\n                            />Medium</label\r\n                        >\r\n                        <label class=\"form-label-radio\">\r\n                            <input\r\n                                type=\"radio\"\r\n                                class=\"form-radio\"\r\n                                v-model=\"fields.taskPriority\"\r\n                                value=\"high\"\r\n                            />High</label\r\n                        >\r\n                    </div>\r\n                    <div class=\"date-grp\">\r\n                        <div class=\"form-group-custom\">\r\n                            <input\r\n                                type=\"text\"\r\n                                name=\"startDate\"\r\n                                :min=\"minDate\"\r\n                                v-model=\"fields.startDate\"\r\n                                onfocus=\"(this.type='date')\"\r\n                                class=\"form-date form-control\"\r\n                                placeholder=\"Task start date\"\r\n                            />\r\n                            <div\r\n                                v-if=\"errors && errors.startDate\"\r\n                                class=\"text-danger\"\r\n                            >\r\n                                {{ errors.startDate[0] }}\r\n                            </div>\r\n                        </div>\r\n                        <div class=\"form-group-custom\">\r\n                            <input\r\n                                type=\"text\"\r\n                                name=\"completionDate\"\r\n                                v-model=\"fields.completionDate\"\r\n                                onfocus=\"(this.type='date')\"\r\n                                class=\"form-date form-control\"\r\n                                placeholder=\"Task end date\"\r\n                            />\r\n                            <div\r\n                                v-if=\"errors && errors.completionDate\"\r\n                                class=\"text-danger\"\r\n                            >\r\n                                {{ errors.completionDate[0] }}\r\n                            </div>\r\n                        </div>\r\n                        <div class=\"form-group-custom\">\r\n                            <input\r\n                                type=\"text\"\r\n                                name=\"startTime\"\r\n                                v-model=\"fields.startTime\"\r\n                                onfocus=\"(this.type='time')\"\r\n                                placeholder=\"Task start time\"\r\n                                class=\"form-date form-control\"\r\n                            />\r\n                            <div\r\n                                v-if=\"errors && errors.startTime\"\r\n                                class=\"text-danger\"\r\n                            >\r\n                                {{ errors.startTime[0] }}\r\n                            </div>\r\n                        </div>\r\n                        <div class=\"form-group-custom\">\r\n                            <input\r\n                                type=\"text\"\r\n                                name=\"completionTime\"\r\n                                v-model=\"fields.completionTime\"\r\n                                onfocus=\"(this.type='time')\"\r\n                                class=\"form-date form-control\"\r\n                                placeholder=\"Task end time\"\r\n                            />\r\n                            <div\r\n                                v-if=\"errors && errors.completionTime\"\r\n                                class=\"text-danger\"\r\n                            >\r\n                                {{ errors.completionTime[0] }}\r\n                            </div>\r\n                        </div>\r\n                    </div>\r\n                    <div class=\"form-group-custom ml-5\">\r\n                        <button\r\n                            v-if=\"editForm == false\"\r\n                            @click.prevent=\"addNewTask\"\r\n                            class=\"btn wed-btn-main btn-sm\"\r\n                        >\r\n                            Submit\r\n                        </button>\r\n                        <button\r\n                            v-if=\"editForm == true\"\r\n                            @click.prevent=\"updateTask\"\r\n                            class=\"btn wed-btn-main btn-sm\"\r\n                        >\r\n                            Update\r\n                        </button>\r\n                    </div>\r\n                </form>\r\n            </div>\r\n            <div class=\"row mt-3\" v-if=\"tasks.length > 0\">\r\n                <div\r\n                    v-for=\"task in reversedTasks\"\r\n                    :key=\"task.id\"\r\n                    class=\"task-list-single\"\r\n                >\r\n                    <div class=\"single-first-row\">\r\n                        <div>\r\n                            <span>{{ task.category.name }}</span>\r\n                            <span\r\n                                >Assigned to :\r\n                                <span style=\"font-weight: 900\">{{\r\n                                    task.assignable.name\r\n                                }}</span></span\r\n                            >\r\n                        </div>\r\n                        <div>\r\n                            <span>\r\n                                <img src=\"/assets/tag.svg\" alt=\"\" />\r\n                                {{ task.taskPriority }}\r\n                            </span>\r\n                            <span>\r\n                                <img\r\n                                    @click=\"setVal(task)\"\r\n                                    src=\"/assets/edit.svg\"\r\n                                    alt=\"\"\r\n                                />\r\n                                <img\r\n                                    @click=\"deleteTask(task)\"\r\n                                    src=\"/assets/trash.svg\"\r\n                                    alt=\"\"\r\n                                />\r\n                            </span>\r\n                        </div>\r\n                    </div>\r\n                    <div class=\"single-second-row\">\r\n                        {{ task.title }}\r\n                    </div>\r\n                    <div class=\"single-third-row\">\r\n                        <div>\r\n                            <img src=\"/assets/icon-calendar.svg\" alt=\"\" />\r\n                            <span>{{ task.startDate }}&nbsp&nbsp </span> ---\r\n                            <span>&nbsp&nbsp{{ task.completionDate }}</span>\r\n                        </div>\r\n                        <div>\r\n                            <img src=\"/assets/icon-clock.svg\" alt=\"\" />\r\n                            <span>{{ task.startTime }}</span> -\r\n                            <span>{{ task.completionTime }}</span>\r\n                        </div>\r\n                        <div>\r\n                            <span></span>\r\n                            {{ task.taskStatus }}\r\n                        </div>\r\n                    </div>\r\n                </div>\r\n            </div>\r\n            <div v-else>\r\n                <p>\r\n                    Nice job! You'v finished all your your assigned Tasks! add\r\n                    Tasks\r\n                </p>\r\n            </div>\r\n        </div>\r\n    </div>\r\n</template>\r\n\r\n<script>\r\nexport default {\r\n    props: [\"hostId\", \"invitationId\", \"tasksall\"],\r\n    data() {\r\n        return {\r\n            editing: false,\r\n            loading: true,\r\n            selectedMember: null,\r\n            fields: {\r\n                // title:\"\",\r\n                invitation_id: this.invitationId,\r\n                // pinned: false,\r\n                taskPriority: \"low\",\r\n                // taskStatus: \"notStarted\",\r\n                // member_id: \"\",\r\n                // category_id: \"\",\r\n            },\r\n            taskId: \"\",\r\n            taskTitle: \"\",\r\n            taskCategory: \"\",\r\n            taskMember: \"\",\r\n            taskPriority: \"\",\r\n            taskDueDate: \"\",\r\n            taskStatus: \"\",\r\n            errors: {},\r\n            success: false,\r\n            loaded: true,\r\n            hasError: false,\r\n            tasks: this.tasksall,\r\n            members: [],\r\n            categories: [],\r\n            image_srcs: \"/assets/frame881.png\",\r\n            editForm: false,\r\n            showDropdown: false,\r\n        };\r\n    },\r\n    mounted() {\r\n        console.log(\"sssss\");\r\n\r\n        this.getMembers();\r\n        this.getCategories();\r\n        // this.getTasks();\r\n    },\r\n    computed: {\r\n        reversedTasks() {\r\n            return [...this.tasks].reverse().sort();\r\n        },\r\n        minDate() {\r\n            var today = new Date();\r\n            var dd = today.getDate();\r\n            var mm = today.getMonth() + 1; //January is 0 so need to add 1 to make it 1!\r\n            var yyyy = today.getFullYear();\r\n            if (dd < 10) {\r\n                dd = \"0\" + dd;\r\n            }\r\n            if (mm < 10) {\r\n                mm = \"0\" + mm;\r\n            }\r\n\r\n            today = yyyy + \"-\" + mm + \"-\" + dd;\r\n            return today;\r\n        },\r\n    },\r\n    methods: {\r\n        setMember(mem) {\r\n            console.log(\"heloooooooooooooooo\");\r\n            this.selectedMember = mem;\r\n        },\r\n        saveTask() {\r\n            this.tasks.push({\r\n                id: this.tasks.length + 1,\r\n                title: this.fields.title,\r\n                taskPriority: this.fields.taskPriority,\r\n                taskStatus: this.fields.taskStatus,\r\n                dueDate: this.fields.dueDate,\r\n                pinned: this.fields.pinned,\r\n                member_id: this.fields.member_id,\r\n                category_id: this.fields.category_id,\r\n            });\r\n            this.fields.title = \"\";\r\n            this.fields.taskPriority = \"low\";\r\n            this.fields.taskStatus = \"\";\r\n            this.fields.dueDate = \"\";\r\n            this.fields.member_id - \"\";\r\n            this.fields.category_id = \"\";\r\n            this.selectedMember = null;\r\n        },\r\n        doEdit(editing) {\r\n            this.editing = editing;\r\n            this.fields.title = \"\";\r\n            this.fields.taskPriority = \"low\";\r\n            this.fields.taskStatus = \"\";\r\n            this.fields.startDate = \"\";\r\n            this.fields.completionDate = \"\";\r\n            this.fields.startTime = \"\";\r\n            this.fields.completionTime = \"\";\r\n            this.fields.invitation_id = this.invitationId;\r\n            (this.member_id = {}), (this.fields.category_id = \"\");\r\n            this.editForm = false;\r\n            this.taskId = null;\r\n            this.selectedMember = null;\r\n        },\r\n        togglePinned(task) {\r\n            task.pinned = !task.pinned;\r\n        },\r\n\r\n        addNewTask() {\r\n            var _this = this;\r\n            if (this.loaded) {\r\n                this.loaded = false;\r\n                this.success = false;\r\n                this.errors = {};\r\n                const taskStatus = \"Not Started\";\r\n                let type = \"member\";\r\n                if (this.selectedMember?.roles_user) {\r\n                    type = \"host\";\r\n                }\r\n                this.fields = { ...this.fields, taskStatus };\r\n                this.fields = { ...this.fields, type };\r\n                axios\r\n                    .post(\"/submittask\", this.fields)\r\n                    .then((response) => {\r\n                        _this.getTasks();\r\n                        _this.loading = false;\r\n                        _this.success = true;\r\n                        _this.fields = {\r\n                            invitation_id: this.invitationId,\r\n                            taskPriority: \"low\",\r\n                        }; //Clear input fields.\r\n                        _this.selectedMember = null;\r\n                        _this.loaded = true;\r\n                        _this.success = true;\r\n                        _this.editing = false;\r\n                    })\r\n                    .catch((error) => {\r\n                        _this.loaded = true;\r\n                        if (error.response.status === 422) {\r\n                            _this.errors = error.response.data.errors || {};\r\n                        }\r\n                    });\r\n            }\r\n        },\r\n        getMembers() {\r\n            var _this = this;\r\n            axios\r\n                .get(\"/getmembers\")\r\n                .then(function (response) {\r\n                    _this.members = response.data;\r\n                })\r\n                .catch((error) => {\r\n                    console.log(\"Get All: \" + error);\r\n                });\r\n        },\r\n        getCategories: function () {\r\n            var _this = this;\r\n            axios\r\n                .get(route(\"admin.tasks.index\"))\r\n                .then((response) => {\r\n                    _this.categories = response.data.data;\r\n                    console.log(_this.categories);\r\n                })\r\n                .catch(function (error) {\r\n                    console.log(error);\r\n                });\r\n        },\r\n        // getTasks() {\r\n        //     var _this = this;\r\n        //     axios\r\n        //         .get(\"/gettasks\")\r\n        //         .then(function (response) {\r\n        //             _this.tasks = response.data;\r\n        //             console.log(_this.tasks);\r\n        //             _this.loading = false;\r\n        //         })\r\n        //         .catch((error) => {\r\n        //             console.log(\"Get All: \" + error);\r\n        //         });\r\n        // },\r\n        setVal(task) {\r\n            this.doEdit(true);\r\n            this.taskId = task.id;\r\n            this.fields.title = task.title;\r\n            this.fields.category_id = task.category_id;\r\n            this.fields.taskPriority = task.taskPriority;\r\n            this.fields.assignable_id = task.assignable_id;\r\n            this.fields.assignable_type = task.assignable_type;\r\n            this.fields.startDate = new Date(task.startDate)\r\n                .toISOString()\r\n                .split(\"T\")[0];\r\n            this.fields.completionDate = new Date(task.completionDate)\r\n                .toISOString()\r\n                .split(\"T\")[0];\r\n            this.fields.startTime = task.startTime;\r\n            this.fields.completionTime = task.completionTime;\r\n            this.fields.taskStatus = task.taskStatus;\r\n            this.editForm = true;\r\n            //console.log(typeof this.taskDueDate);\r\n        },\r\n        updateTask() {\r\n            var _this = this;\r\n            var id = this.taskId;\r\n            var title = this.fields.title;\r\n            var category_id = this.fields.category_id;\r\n            var taskPriority = this.fields.taskPriority;\r\n            var startDate = this.fields.startDate;\r\n            var completionDate = this.fields.completionDate;\r\n            var startTime = this.fields.startTime;\r\n            var completionTime = this.fields.completionTime;\r\n            var taskStatus = this.fields.taskStatus;\r\n            var assignable_id = this.fields.assignable_id;\r\n            var assignable_type = this.fields.assignable_type;\r\n            //console.log(id,title,category_id,member_id,taskPriority,dueDate,taskStatus);\r\n            //   axios({\r\n            //     method: \"post\",\r\n            //     url: \"/updatetask/\" + id,\r\n            //     data: {\r\n            //       title: title,\r\n            //       category_id: category_id,\r\n            //       member_id: member_id,\r\n            //       taskPriority: taskPriority,\r\n            //       dueDate: dueDate,\r\n            //       taskStatus: taskStatus,\r\n            //     },\r\n            //   })\r\n            //     .then(response => _this.getTasks())\r\n            //     .catch(error => console.log(error));\r\n            axios\r\n                .patch(\r\n                    \"/updatetask/\" + id,\r\n                    {\r\n                        title: title,\r\n                        category_id: category_id,\r\n                        member_id: member_id,\r\n                        taskPriority: taskPriority,\r\n                        startDate: startDate,\r\n                        completionDate: completionDate,\r\n                        startTime: startTime,\r\n                        completionTime: completionTime,\r\n                        taskStatus: taskStatus,\r\n                        assignable_id: assignable_id,\r\n                        assignable_type: assignable_type,\r\n                    },\r\n                    {\r\n                        headers: {\r\n                            \"Content-Type\": \"application/json\",\r\n                            // Authorization: 'Bearer ' + varToken\r\n                        },\r\n                    }\r\n                )\r\n                .then((response) => {\r\n                    _this.getTasks();\r\n                    _this.loading = false;\r\n                    _this.doEdit(false);\r\n                    //console.log($(\"#myModal button:nth-child(1)\"));\r\n                    // $(\"#myModal button:nth-child(1)\").attr(\r\n                    //     \"data-dismiss\",\r\n                    //     \"modal\"\r\n                    // );\r\n                    //$(\"#myModal button:nth-child(1)\").click();\r\n                    //console.log(\"ss\");\r\n                })\r\n                .catch((error) => console.log(error));\r\n        },\r\n        deleteTask(task) {\r\n            var _this = this;\r\n            axios\r\n                .post(\"/deletetask/\" + task.id)\r\n                .then(function (response) {\r\n                    _this.getTasks();\r\n                })\r\n                .catch((error) => {\r\n                    console.log(\"Delete task: \" + error);\r\n                });\r\n        },\r\n        closeForm() {\r\n            this.showDropdown = false;\r\n        },\r\n    },\r\n    created: function () {\r\n        window.addEventListener(\"click\", this.closeForm);\r\n    },\r\n    computed: {\r\n        reversedTasks() {\r\n            return [...this.tasks].reverse().sort();\r\n        },\r\n    },\r\n};\r\n</script>\r\n\r\n<style scoped>\r\n.counter {\r\n    font-size: 0.8rem;\r\n    padding-left: 10px;\r\n    padding-right: 10px;\r\n}\r\n\r\n#task-list {\r\n    padding: 1rem;\r\n    border-radius: 3px;\r\n    /* box-shadow: 0 4px 8px 0 rgba(0, 0, 0, 0.12), 0 2px 4px 0 rgba(0, 0, 0, 0.08); */\r\n    width: 95%;\r\n    max-width: 100%;\r\n}\r\n\r\nh1 {\r\n    color: #3d4852;\r\n}\r\n\r\nul {\r\n    list-style: none;\r\n    padding: 0;\r\n}\r\n\r\na {\r\n    color: #6cb2eb;\r\n    font-size: 1.25rem;\r\n    transition: all 0.1s ease-in;\r\n    margin-top: 0.5rem;\r\n    display: block;\r\n}\r\n\r\na:hover {\r\n    color: #3490dc;\r\n}\r\n\r\nli,\r\np {\r\n    display: flex;\r\n    align-items: center;\r\n    letter-spacing: 0.5px;\r\n    color: #3d4852;\r\n    cursor: pointer;\r\n    transition: all 0.1s ease-in;\r\n}\r\n\r\nli:hover {\r\n    color: #22292f;\r\n}\r\n\r\nli input {\r\n    margin: 0 0.5rem 0;\r\n}\r\n\r\n#task-list > input,\r\n#task-list > select {\r\n    width: 100%;\r\n    border-radius: 3px;\r\n    box-shadow: 0 2px 4px 0 rgba(0, 0, 0, 0.1);\r\n    border: 1px solid #f1f5f8;\r\n    color: #606f7b;\r\n    padding: 0.5rem 0.75rem;\r\n    box-sizing: border-box;\r\n    font-size: 1rem;\r\n    letter-spacing: 0.5px;\r\n    margin: 0.5rem 0;\r\n}\r\n\r\n.btn {\r\n    border: none;\r\n    border-radius: 3px;\r\n    margin: auto 0;\r\n    padding: 0.5rem 0.75rem;\r\n    flex-shrink: 0;\r\n    cursor: pointe